import React, { Suspense } from 'react';
import './teller.css';
import { Outlet } from 'react-router-dom';
import AdminProviders from '../../../context/providers/AdminProviders';
import config from '../../../config/config';
// Redux
import { useDispatch, useSelector } from 'react-redux';
import { logout, me as getMe } from '../../../redux/async/authAsync';
// Services
import storage from '../../../services/storage/storageService';
import alertService from '../../../services/alert/alertService';
// Page Styling
import { styled } from '@mui/material/styles'; 
// MUI Components
import Box from '@mui/material/Box';
import MuiDrawer from '@mui/material/Drawer';
import MuiAppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import Tooltip from '@mui/material/Tooltip';
// Page Components
import Copyright from '../../components/shared/Copyright/Copyright';
import Brand from '../../components/shared/Brand/Brand';
import NavItem from '../../components/nav/NavItem';
import NavCollapse from '../../components/nav/NavCollapse';
import Overlay from '../../components/shared/Overlay/Overlay';
import Messages from '../../components/shared/Messages/Messages';
import Notifications from '../../components/shared/Notifications/Notifications';
import Help from '../../components/shared/Help/Help';
import AccountMenu from '../../components/shared/Menu/TellerMenu';
import ChangePinModal from '../../components/modals/ChangePinModal';
import Clock from '../../components/shared/Clock/Clock';
// React-icons
import { MdExitToApp } from 'react-icons/md';
// Navigation Menu
import { menu } from '../../components/nav/config/teller';
import utilityService from '../../../services/utils/utilityServices';

const closedMixin = (theme) => ({
  transition: theme.transitions.create('width', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  overflowX: 'hidden',
  width: `calc(${theme.spacing(7)} + 1px)`,
  [theme.breakpoints.up('sm')]: {
    width: `calc(${theme.spacing(9)} + 1px)`,
  },
});

const DrawerHeader = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'flex-end',
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
}));

const AppBar = styled(MuiAppBar, { shouldForwardProp: (prop) => prop !== 'open' })(
  ({ theme, open, drawerwidth }) => ({
    ...(!open && {
      marginLeft: 0,
      width: `calc(100% - ${drawerwidth}px)`,
    }),
  }));

const Drawer = styled(MuiDrawer, { shouldForwardProp: (prop) => prop !== 'open' })(
  ({ theme, open, drawerwidth }) => ({
    width: drawerwidth,
    flexShrink: 0,
    whiteSpace: 'nowrap',
    boxSizing: 'border-box',
    ...(!open && {
      ...closedMixin(theme),
      '& .MuiDrawer-paper': closedMixin(theme),
    }),
  }),
);

export default function Teller(props) {

  const dispatch = useDispatch();

  const authRedux = useSelector((state) => state.auth.value);
  const meRedux = authRedux?.me;
  const role = utilityService.capitalizeFirstLetter(authRedux?.role?.role);
  const firstName = utilityService.capitalizeFirstLetter(meRedux?.employee?.first_name);

  let avatar_path = null;

  if (meRedux?.avatar !== null && meRedux?.avatar !== undefined) {
    avatar_path = `${config.RESOURCES}users/${meRedux?.uuid}/files/avatar/${meRedux?.avatar}`;
  }

  const [changePinModal, setChangePinModal] = React.useState(false);
  const toggleChangePinModal = () => setChangePinModal(!changePinModal);

  const open = false;
  const drawerWidth = 70;
  const navigation = menu

  const handleLogout = async () => {

    let response = await alertService.confirm({ title: "Logout", text: "You are now exiting?" });

    if (response.isConfirmed) {
      alertService.process("Logging out...");
      await dispatch(logout()).unwrap();
      storage.reset();
      window.location.href = "/";
    }
  }

  const getMeAsync = React.useCallback(async () => {
    await dispatch(getMe()).unwrap();
  }, [dispatch])

  React.useEffect(() => {
    let mounted = false;

    if (!mounted) {
      getMeAsync();
    }

    return () => mounted = true;

  }, [getMeAsync, dispatch])

  return (
    <AdminProviders>
      {changePinModal && <ChangePinModal open={changePinModal} toggle={toggleChangePinModal} />}
      <Box sx={{ display: 'flex', overflow: 'hidden' }}>
        <CssBaseline />
        <AppBar elevation={1} position="fixed" open={open} drawerwidth={drawerWidth}>
          <Toolbar>
            <Grid container>
              <Grid xs={3} item>
                <Box display="flex" justifyContent="flex-start">
                  <Box display="flex" flexDirection="row" alignItems="center" justifyContent="center" sx={{ mt: 1 }}>
                    <Clock showIcon={true} />
                    <Typography
                      fontSize={20}
                      sx={{
                        "&:before": {
                          content: '"-"',
                          ml: 1,
                          mr: 1,
                          mt: 2
                        }
                      }}>
                      {utilityService.capitalizeFirstLetter(authRedux?.branch?.branch)} Branch
                    </Typography>
                  </Box>
                </Box>
              </Grid>
              <Grid xs={9} item>
                <Box display="flex" justifyContent="flex-end" sx={{ marginLeft: 20 }}>
                  <Typography
                    fontSize={18}
                    sx={{
                      mt: 1.7,
                      mr: 1,
                      "&:after": {
                        content: '"|"',
                        ml: 1,
                        mt: 2
                      }
                    }}>
                    Welcome {firstName ?? role}
                  </Typography>
                  <Messages />
                  <Notifications />
                  <Help />
                  <AccountMenu profile={avatar_path} handleChangePin={() => toggleChangePinModal()} />
                  <Tooltip arrow={true} title={"Logout?"} placement="bottom">
                    <IconButton color="inherit" style={{ backgroundColor: 'transparent' }} onClick={() => handleLogout()}>
                      <Typography>
                        Exit
                      </Typography>
                      <MdExitToApp size={20} style={{ marginLeft: 5 }} />
                    </IconButton>
                  </Tooltip>
                </Box>
              </Grid>
            </Grid>
          </Toolbar>
        </AppBar>
        <Drawer variant="permanent" open={open}>
          <DrawerHeader sx={{ backgroundColor: open && "#00BFA5" }}>
            <Brand open={open} width={drawerWidth} />
          </DrawerHeader>
          <Divider sx={{ mt: open ? 9.8 : 0 }} />
          <Box display="flex" flexDirection="column" justifyContent="space-between" flexGrow={1}>
            <List disablePadding={true}>
              <Box>
                {navigation.map((value, key) => {
                  if (!value.collapse) {
                    return <NavItem options={value} index={key} open={open} key={`ListItemMenu-${key}`} history={props.history} />
                  }
                  return <NavCollapse index={key} options={value} open={open} key={`ListItemMenu-${key}`} history={props.history} />

                })}
              </Box>
            </List>
            <List>
              {open && (
                <Box sx={{ ml: 1 }}>
                  <Copyright alignment="left" />
                </Box>
              )}
            </List>
          </Box>
        </Drawer>
        <Box component="main" sx={{ flexGrow: 1, p: 0, margin: 0, overflow: 'hidden' }}>
          <DrawerHeader />
          <Grid container>
            <Grid item xs={12}>
              <Box square sx={{ m: 0, height: '100vh', overflow: 'hidden', backgroundColor: "#FFF" }}>
                <Suspense fallback={<Overlay isDrawerOpen={open} isLoading={true} zindex={2} opacity={0.5} />}>
                  <Outlet />
                </Suspense>
              </Box>
            </Grid>
          </Grid>
        </Box>
      </Box >
    </AdminProviders>
  );
}
