import React from 'react';
import { Box, IconButton, Paper, TextField } from '@mui/material';
import { FaKey, FaTimes } from 'react-icons/fa';
import { IoMdKeypad } from 'react-icons/io';
import { LoadingButton } from '@mui/lab';
import toastService from '../../../services/toast/toastService';
import pinRepository from '../../../repositories/pinRepository';

export default function ChangePinForm(props) {

  const [oldPin, setOldPin] = React.useState("");
  const [newPin, setNewPin] = React.useState("");
  const [changing, setChanging] = React.useState(false);

  const handleOldPin = (pin) => {
    if (oldPin.length < 7) {
      setOldPin(pin);
    } else {
      toastService.alert("Max pin length is 6");
    }
  }

  const handleNewPin = (pin) => {
    if (newPin.length < 7) {
      setNewPin(pin);
    } else {
      toastService.alert("Max pin length is 6");
    }
  }

  const handleChangePin = async () => {
    try {

      setChanging(true);

      let data = {
        oldPin: oldPin,
        newPin: newPin
      }

      let result = await pinRepository.change(data);

      if (!result.error) {
        toastService.success(result.message);
        props.toggle()
      } else {
        toastService.error(result.message);
      }

    } catch (e) {
      toastService.error(e.toString());
    } finally {
      setChanging(false);
    }
  }

  return (
    <div>
      <Box sx={{ minWidth: 500, maxWidth: 500 }}>
        <Paper square sx={{ m: 1, p: 2 }}>
          <TextField
            autoComplete='off'
            type="password"
            variant='outlined'
            value={oldPin}
            onChange={(e) => handleOldPin(e.target.value)}
            label="Enter Old PIN"
            fullWidth
            size="small"
            InputProps={{
              endAdornment: oldPin.length > 0 &&
                <IconButton disableFocusRipple disableRipple onClick={() => setOldPin("")}>
                  <FaTimes size={12} />
                </IconButton>
            }}
            inputProps={{
              minLength: 6,
              maxLength: 6
            }}
            sx={{ mt: 2 }}
          />
          <TextField
            autoComplete='off'
            type="password"
            variant='outlined'
            value={newPin}
            onChange={(e) => handleNewPin(e.target.value)}
            label="Enter New PIN"
            fullWidth
            size="small"
            InputProps={{
              endAdornment: newPin.length > 0 &&
                <IconButton disableFocusRipple disableRipple onClick={() => setNewPin("")}>
                  <FaTimes size={12} />
                </IconButton>
            }}
            inputProps={{
              minLength: 6,
              maxLength: 6
            }}
            sx={{ mt: 2 }}
          />
          <Box display="flex" flexDirection="row" justifyContent="flex-end" sx={{ mt: 2 }}>
            <LoadingButton loading={changing} variant="contained" sx={{ minWidth: 120 }} onClick={handleChangePin}>Change</LoadingButton>
            <LoadingButton loading={changing} variant="contained" color="secondary" sx={{ ml: 1, minWidth: 120 }} onClick={props.toggle}>Cancel</LoadingButton>
          </Box>
        </Paper>
      </Box>
    </div>
  )
}
