import { createContext, useState } from "react";

const MemberAccountContext = createContext({});

export const MemberAccountProvider = ({ children }) => {

    const [accountType, setAccountType] = useState("No Account Set");
    const [accountNumber, setAccountNumber] = useState("");
    const [balance, setBalance] = useState(0.00);
    const [maintainingBalance, setMaintainingBalance] = useState(0.00);
    const [minInitialDeposit, setMinInitialDeposit] = useState(0.00);
    const [lastAmount, setLastDeposit] = useState(0.00);
    const [lastDate, setLastTransactionDate] = useState("N/A");

    const resetAccount = () => {
        setAccountNumber("");
        setAccountType("");
        setBalance(0.00);
        setLastDeposit(0.00);
        setLastTransactionDate("N/A");
        setMaintainingBalance(0.00);
        setMinInitialDeposit(0.00);
    }

    return (
        <MemberAccountContext.Provider value={{
            accountType, setAccountType,
            accountNumber, setAccountNumber,
            balance, setBalance,
            lastAmount, setLastDeposit,
            lastDate, setLastTransactionDate,
            maintainingBalance, setMaintainingBalance,
            minInitialDeposit, setMinInitialDeposit,
            resetAccount
        }}>
            {children}
        </MemberAccountContext.Provider>
    )
}

export default MemberAccountContext;