import React from 'react'
// Modal Components
import InnerModalDialog from '../shared/Dialog/InnerModalDialog';
import ChangePinForm from '../forms/ChangePinForm';
import { Typography } from '@mui/material';
import { IoMdKeypad } from 'react-icons/io';

export default function ChangePinModal(props) {


    return (
        <div>
            <InnerModalDialog open={props.open} title={<Typography variant='h6'><IoMdKeypad style={{ mt: 2 }} size={15} /> Change Pin</Typography>} toggle={props.toggle}>
                <ChangePinForm toggle={props.toggle} />
            </InnerModalDialog>
        </div>
    )
}
